	methods: {
		starClick : function(){
			//TODO 判断是否登录主要判断全局是否存在userid
			if(this.icons.star == "star"){
				uni.request({
					//TODO 替换为收藏请求
					url: 'http://www.baidu.com?type=star',
					method: 'GET',
					data: {},
					success: res => {
						this.icons.star = "star-filled";
						this.icons.starColor = "#FF0000"
						this.starCount = ++this.starCount
						console.log(this.starCount);
						uni.showToast({
							icon: "none",
							title: '收藏成功',
							duration: 2000
						});
					},
					fail: () => {
						console.log("收藏失败！");
						uni.showToast({
						    icon: "none", 
							title: '收藏失败！',
						    duration: 2000
						});
					},
					complete: () => {}
				});
			}else{
				uni.request({
					//TODO 替换为收藏请求
					url: 'http://www.baidu.com?type=unstar',
					method: 'GET',
					data: {},
					success: res => {
						this.icons.star = "star";
						this.icons.starColor = "#ddd"
						this.starCount = --this.starCount
						uni.showToast({
							icon: "none",
							title: '取消收藏成功',
							duration: 2000
						});
					},
					fail: () => {
						console.log("收藏失败！");
						uni.showToast({
						    icon: "none", 
							title: '取消收藏失败！',
						    duration: 2000
						});
					}
				});
			}
		},
