		},
		chatConfirm : function(e){
			if(e.detail.value == ""){
				uni.showToast({
					icon: "none",
					title: '评论内容不能为空~',
					duration: 2000
				});
				return;
			}
			uni.request({
				//TODO 替换为评论请求
				url: 'http://www.baidu.com',
				method: 'GET',
				data: {},
				success: res => {
					uni.showToast({
						icon: "none",
						title: '评论成功',
						duration: 2000
					});
					this.chatList.push({
						nickName:"狗蛋",
						chatContent: this.chatContent,
						createAt:"2019-10-10 19:01:29"
					})
					 
					this.chatContent = "";
				},
				fail: () => {
					uni.showToast({
					    icon: "none", 
						title: '评论失败！',
					    duration: 2000
					}); 
				}
			});
		}
